// app/components/Navbar.tsx - AVEC MENU ACTIF
'use client'

import { useAuth } from '@/contexts/AuthContext'
import Link from 'next/link'
import { useState, useEffect } from 'react'
import { usePathname } from 'next/navigation'
import { Menu, X, Sparkles } from 'lucide-react'
import { motion, AnimatePresence } from 'framer-motion'

export default function Navbar() {
  const { user, isAuthenticated, isAdmin, isWebmaster, logout } = useAuth()
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const [scrolled, setScrolled] = useState(false)
  const pathname = usePathname()

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 20)
    }
    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  // Fonction pour v√©rifier si un lien est actif
  const isActive = (href: string) => {
    if (href === '/') {
      return pathname === '/'
    }
    
    // Pour les liens vers les pages CDEJ, v√©rifier si on est dans cette section
    if (href === '/ministries' && pathname.startsWith('/ministries/cdej-rafissa')) {
      return true
    }
    
    return pathname.startsWith(href)
  }

  // Menu avec seulement /ministries
  const menuItems = [
    { label: 'Accueil', href: '/' },
    { label: '√Ä propos', href: '/about' },
    { label: 'Croyances', href: '/beliefs' },
    { label: 'Minist√®res', href: '/ministries' }, // Page des cartes des minist√®res
    { label: '√âv√©nements', href: '/events' },
    { label: 'Messages', href: '/sermons' },
    { label: 'Contact', href: '/contact' },
  ]

  return (
    <nav className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
      scrolled 
        ? 'bg-white/95 backdrop-blur-xl shadow-2xl py-3' 
        : 'bg-gradient-to-b from-white/95 to-white/80 backdrop-blur-sm py-4'
    }`}>
      <div className="max-w-7xl mx-auto px-4">
        <div className="flex justify-between items-center">
          {/* Logo avec effet scintillant */}
          <Link href="/" className="flex items-center space-x-3 group">
            <motion.div 
              className="relative"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              <div className="w-12 h-12 bg-gradient-to-br from-blue-600 via-purple-600 to-pink-600 rounded-xl flex items-center justify-center relative overflow-hidden">
                {/* Effet de brillance */}
                <motion.div
                  className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent"
                  animate={{
                    x: ['-100%', '100%'],
                  }}
                  transition={{
                    duration: 2,
                    repeat: Infinity,
                    repeatType: 'loop',
                  }}
                />
                <span className="text-white font-bold text-xl relative z-10">EGG</span>
              </div>
              <Sparkles className="absolute -top-2 -right-2 w-4 h-4 text-yellow-400" />
            </motion.div>
            <div className="hidden sm:block">
              <div className="font-bold text-gray-900 text-lg">√âglise Grande Gr√¢ce</div>
              <div className="text-xs text-gray-500">D√©fal√© - CDEJ-RAFISSA</div>
            </div>
          </Link>

          {/* Navigation desktop avec menu actif */}
          <div className="hidden lg:flex items-center space-x-1">
            {menuItems.map((item, index) => {
              const active = isActive(item.href)
              return (
                <Link
                  key={index}
                  href={item.href}
                  className={`px-4 py-2 transition-colors font-medium rounded-lg relative ${
                    active
                      ? 'text-blue-600 bg-blue-50/80'
                      : 'text-gray-700 hover:text-blue-600 hover:bg-blue-50/50'
                  }`}
                >
                  {item.label}
                  {/* Indicateur visuel pour le menu actif */}
                  {active && (
                    <motion.div
                      layoutId="activeIndicator"
                      className="absolute -bottom-1 left-0 right-0 h-0.5 bg-blue-600 rounded-full"
                      initial={false}
                      transition={{
                        type: "spring",
                        stiffness: 380,
                        damping: 30
                      }}
                    />
                  )}
                </Link>
              )
            })}
            
            {/* Lien admin conditionnel */}
            {(isAdmin || isWebmaster) && (
              <Link
                href="/admin"
                className={`px-4 py-2 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-lg hover:from-blue-700 hover:to-purple-700 transition-all font-medium ml-2 shadow-lg ${
                  pathname.startsWith('/admin') ? 'ring-2 ring-blue-300 ring-offset-2' : ''
                }`}
              >
                Administration
              </Link>
            )}
          </div>

          {/* Menu utilisateur / Connexion */}
          <div className="hidden lg:flex items-center space-x-4">
            {isAuthenticated ? (
              <div className="relative group">
                <button className="flex items-center space-x-3 focus:outline-none">
                  <div className="text-right">
                    <div className="font-medium text-gray-900 text-sm">
                      {user?.first_name} {user?.last_name}
                    </div>
                    <div className="text-xs text-gray-500 flex items-center gap-1">
                      <Sparkles className="w-3 h-3" />
                      {isWebmaster ? 'Webmaster' : isAdmin ? 'Admin' : 'Membre'}
                    </div>
                  </div>
                  <div className={`w-10 h-10 bg-gradient-to-br from-blue-100 to-purple-100 rounded-full flex items-center justify-center border-2 ${
                    pathname.startsWith('/profile') ? 'border-blue-500' : 'border-white'
                  } shadow-lg relative overflow-hidden`}>
                    <motion.div
                      className="absolute inset-0 bg-gradient-to-r from-transparent via-white/30 to-transparent"
                      animate={{
                        x: ['-100%', '100%'],
                      }}
                      transition={{
                        duration: 3,
                        repeat: Infinity,
                        repeatType: 'loop',
                      }}
                    />
                    <span className="text-blue-600 font-semibold text-sm relative z-10">
                      {user?.first_name?.charAt(0)}{user?.last_name?.charAt(0)}
                    </span>
                  </div>
                </button>

                {/* Dropdown menu utilisateur */}
                <div className="absolute right-0 top-full pt-2 opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200">
                  <div className="bg-white rounded-xl shadow-2xl border border-gray-100 py-2 min-w-[200px]">
                    <Link
                      href="/profile"
                      className={`flex items-center gap-3 px-6 py-3 transition-colors ${
                        pathname.startsWith('/profile') 
                          ? 'text-blue-600 bg-blue-50/50' 
                          : 'text-gray-700 hover:text-blue-600 hover:bg-blue-50/50'
                      }`}
                    >
                      <div className="w-8 h-8 bg-gray-100 rounded-lg flex items-center justify-center">
                        <span className="text-sm">üë§</span>
                      </div>
                      Mon profil
                    </Link>
                    
                    {(isAdmin || isWebmaster) && (
                      <Link
                        href="/admin"
                        className={`flex items-center gap-3 px-6 py-3 transition-colors ${
                          pathname.startsWith('/admin') 
                            ? 'text-blue-600 bg-blue-50/50' 
                            : 'text-gray-700 hover:text-blue-600 hover:bg-blue-50/50'
                        }`}
                      >
                        <div className="w-8 h-8 bg-blue-100 rounded-lg flex items-center justify-center">
                          <span className="text-sm">‚öôÔ∏è</span>
                        </div>
                        Administration
                      </Link>
                    )}
                    
                    <div className="border-t border-gray-200 my-1"></div>
                    
                    <button
                      onClick={logout}
                      className="flex items-center gap-3 w-full text-left px-6 py-3 text-red-600 hover:bg-red-50/50 transition-colors"
                    >
                      <div className="w-8 h-8 bg-red-100 rounded-lg flex items-center justify-center">
                        <span className="text-sm">üö™</span>
                      </div>
                      Se d√©connecter
                    </button>
                  </div>
                </div>
              </div>
            ) : (
              <div className="flex items-center space-x-4">
                <Link
                  href="/auth/login"
                  className={`text-gray-700 hover:text-blue-600 transition-colors font-medium ${
                    pathname === '/auth/login' ? 'text-blue-600 font-semibold' : ''
                  }`}
                >
                  Connexion
                </Link>
                <Link
                  href="/auth/register"
                  className={`px-6 py-3 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-lg hover:from-blue-700 hover:to-purple-700 transition-all shadow-lg font-medium ${
                    pathname === '/auth/register' ? 'ring-2 ring-blue-300 ring-offset-2' : ''
                  }`}
                >
                  Rejoindre
                </Link>
              </div>
            )}
          </div>

          {/* Bouton menu mobile */}
          <button
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            className="lg:hidden p-3 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-xl hover:from-blue-700 hover:to-purple-700 transition-all shadow-lg"
            aria-label="Menu mobile"
          >
            {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* Menu mobile avec menu actif */}
        <AnimatePresence>
          {isMenuOpen && (
            <motion.div
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: 'auto' }}
              exit={{ opacity: 0, height: 0 }}
              className="lg:hidden mt-4 overflow-hidden"
            >
              <div className="bg-white rounded-2xl shadow-2xl border border-gray-100 p-6">
                {/* Menu principal mobile */}
                <div className="space-y-2 mb-6">
                  {menuItems.map((item, index) => {
                    const active = isActive(item.href)
                    return (
                      <Link
                        key={index}
                        href={item.href}
                        className={`block px-4 py-3 rounded-lg transition-colors font-medium ${
                          active
                            ? 'text-blue-600 bg-blue-50/80'
                            : 'text-gray-700 hover:text-blue-600 hover:bg-blue-50/50'
                        }`}
                        onClick={() => setIsMenuOpen(false)}
                      >
                        <div className="flex items-center justify-between">
                          <span>{item.label}</span>
                          {active && (
                            <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                          )}
                        </div>
                      </Link>
                    )
                  })}
                </div>

                {/* Section utilisateur mobile */}
                {isAuthenticated ? (
                  <div className="pt-6 border-t border-gray-200">
                    <div className="flex items-center gap-3 mb-4">
                      <div className={`w-12 h-12 bg-gradient-to-br from-blue-100 to-purple-100 rounded-full flex items-center justify-center ${
                        pathname.startsWith('/profile') ? 'ring-2 ring-blue-500' : ''
                      }`}>
                        <span className="text-blue-600 font-semibold">
                          {user?.first_name?.charAt(0)}{user?.last_name?.charAt(0)}
                        </span>
                      </div>
                      <div>
                        <div className="font-medium text-gray-900">
                          {user?.first_name} {user?.last_name}
                        </div>
                        <div className="text-sm text-gray-500">
                          {isWebmaster ? 'Webmaster' : isAdmin ? 'Admin' : 'Membre'}
                        </div>
                      </div>
                    </div>
                    
                    <div className="space-y-2">
                      <Link
                        href="/profile"
                        className={`block px-4 py-3 rounded-lg transition-colors ${
                          pathname.startsWith('/profile') 
                            ? 'text-blue-600 bg-blue-50/50' 
                            : 'text-gray-700 hover:text-blue-600 hover:bg-blue-50/50'
                        }`}
                        onClick={() => setIsMenuOpen(false)}
                      >
                        Mon profil
                      </Link>
                      
                      {(isAdmin || isWebmaster) && (
                        <Link
                          href="/admin"
                          className={`block px-4 py-3 rounded-lg transition-colors ${
                            pathname.startsWith('/admin') 
                              ? 'text-blue-600 bg-blue-50/50' 
                              : 'text-gray-700 hover:text-blue-600 hover:bg-blue-50/50'
                          }`}
                          onClick={() => setIsMenuOpen(false)}
                        >
                          Administration
                        </Link>
                      )}
                      
                      <button
                        onClick={() => {
                          logout()
                          setIsMenuOpen(false)
                        }}
                        className="block w-full text-left px-4 py-3 text-red-600 hover:bg-red-50/50 rounded-lg transition-colors"
                      >
                        Se d√©connecter
                      </button>
                    </div>
                  </div>
                ) : (
                  <div className="pt-6 border-t border-gray-200 space-y-3">
                    <Link
                      href="/auth/login"
                      className={`block w-full px-4 py-3 text-center rounded-lg transition-colors font-medium ${
                        pathname === '/auth/login' 
                          ? 'text-blue-600 bg-blue-50/50' 
                          : 'text-gray-700 hover:text-blue-600 hover:bg-blue-50/50'
                      }`}
                      onClick={() => setIsMenuOpen(false)}
                    >
                      Connexion
                    </Link>
                    <Link
                      href="/auth/register"
                      className={`block w-full px-4 py-3 text-center bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-lg hover:from-blue-700 hover:to-purple-700 transition-all font-medium ${
                        pathname === '/auth/register' ? 'ring-2 ring-blue-300' : ''
                      }`}
                      onClick={() => setIsMenuOpen(false)}
                    >
                      S'inscrire
                    </Link>
                  </div>
                )}
              </div>
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    </nav>
  )
}